import Img from '../img/heart.png'
const img = new Image();
img.src = Img;

const avatar = document.querySelector(".avatar");
const avatarBorder = document.querySelector(".avatarBorder");
const mainCanvas = document.getElementById("myCanvas");
const height = mainCanvas.offsetWidth * 0.55 + 'px'
mainCanvas.style.height = height
avatar.style.height = height;
avatarBorder.style.height = mainCanvas.offsetWidth * 0.55 + 38 +'px';
const mainContext = mainCanvas.getContext('2d');


let hearts = [];

const drawCircles = (count) => {
  console.log(123123)
  for (let i = 0; i < count; i++) {
    const randomX = Math.round(1 + Math.random() * mainCanvas.width);
    const randomY = -mainCanvas.height
    const speed = 1.6 + Math.random() * 2 ;

    const circle = new Heart(100, speed, randomX, randomY);
    hearts.push(circle);
  }
}


const likes = document.querySelector('.likes')
likes.onclick = () => drawCircles(6)

class Heart {
  constructor(radius, speed, xPos, yPos) {
    this.speed = speed;
    this.xPos = xPos;
    this.yPos = yPos;
    this.id = Math.random()
    this.update = () => {
      this.yPos = this.yPos + this.speed
      if (this.yPos > 300) hearts = hearts.filter(el => el.id !== this.id)
      mainContext.drawImage(img, this.xPos, -this.yPos, 20, 15)
    }
  }
}

const Likes = () => {

  function draw() {
    mainContext.clearRect(0, 0, 500, 500);

    for (let i = 0; i < hearts.length; i++) {
      const myCircle = hearts[i];
      myCircle.update();
    }
    requestAnimationFrame(draw);
  }
  draw()
}

export default Likes
